/**
 * Contexto de React donde se almacenan los datos del usuario y los temas de la aplicacin
 * til para guardar los datos de personalizacin y temas de la app
 * Ir creciendo a medida que los datos necesarios para la app se incrementen
 */

/**
 * POR HACER
 * Redireccionar a la personalizacin si no existen datos o preferencias de usuario
 */

import React, { createContext, useEffect, useRef, useState } from 'react';

export const UserDataContext = createContext({
    preferenciasUsuario:{
      avatar:'',
      ropa:{}
    }
});

export function UserDataProvider({ children }) {

  const [userData, _setUserData] = useState({
    preferenciasUsuario: {
      avatar:'',
      ropa:{}
    }
  });

  const userDataRef = useRef(userData);

  const setUserData = (data) => {
    // console.log('cambiando', data)
    userDataRef.current = data;
    _setUserData(data);
  }

  useEffect(()=>{

    //  Se buscan datos en el localStorage para cargarlos
    try{
      const dataPreferencias = JSON.parse (localStorage.getItem('preferenciasUsuario') );
      if(dataPreferencias && dataPreferencias!={}){
        setUserData(dataPreferencias);
      }
    }catch( error ){
      console.log('error leyendo localstorage', error);
    }

    
    //  Se escucha al evento de actualizacin de datos que proviene de character-personalizarion (AFRAME COMPONENT)
    document.addEventListener('update_user_prefs', updateUserPrefs, false);

    //  Se escucha al evento de actualizacin del color de piel (AFRAME COMPONENT)
    document.addEventListener('update_skin_color', updateSkinColor, false);
    
    //  Remover el eventlistener al desmontarse el componente
    return () => {
      document.removeEventListener('update_user_prefs', updateUserPrefs);
      document.removeEventListener('update_skin_color', updateSkinColor);
    }

  }, []);

  // useEffect(()=>{
  //   console.log('userdata actualizado',userData);
  // }, [userData]);

  const updateUserPrefs = (e) => {

    let userDataCopy = {...userDataRef.current};
    const dataPrenda = e.detail;
    // console.log('color', dataPrenda.color);

    if( userDataCopy.preferenciasUsuario && userDataCopy.preferenciasUsuario.ropa ){
      userDataCopy.preferenciasUsuario.ropa[dataPrenda.grupo] = {
        ...userDataCopy.preferenciasUsuario.ropa[dataPrenda.grupo],
        meshSelected: dataPrenda.prenda,
      }
      
      const datamaterials = userDataCopy.preferenciasUsuario.ropa.materiales;
      // console.log(datamaterials)
      if(datamaterials && datamaterials.length > 0){
        const material = datamaterials.find(mat => mat.material === dataPrenda.material);
        
        if(material){
          material.color = dataPrenda.color
        }
        else{
          datamaterials.push({
            material:dataPrenda.material,
            color: dataPrenda.color
          })
        }
      }else{
        userDataCopy.preferenciasUsuario.ropa.materiales = [
          {
            material:dataPrenda.material,
            color: dataPrenda.color
          }
        ]
      }

      let dataGroup = userDataCopy.preferenciasUsuario.ropa[dataPrenda.grupo];
      if(dataGroup.opciones){
          let opcion = dataGroup.opciones.find(el => el.mesh === dataPrenda.prenda);
          if(opcion){
            opcion.color = dataPrenda.color
          }
          else{
            dataGroup.opciones.push( {
              mesh : dataPrenda.prenda,
              material:dataPrenda.material,
              color: dataPrenda.color
            })
          }
        
      }
      else{
        dataGroup.opciones = [{
          mesh : dataPrenda.prenda,
          color: dataPrenda.color
        }]
      }
      
    }
    
    setUserData(userDataCopy);
    
  }


  const updateSkinColor = (e) => {

    const color = e.detail.colorPiel;

    let userDataCopy = {...userDataRef.current};
    userDataCopy.preferenciasUsuario.colorPiel = color;

    setUserData(userDataCopy);
    // console.log(userDataCopy);
  }

  
  return (
    <UserDataContext.Provider value={{ userData, setUserData }}>
      {children}
    </UserDataContext.Provider>
  );
}