import axios from "axios";

export const getProducts = (id_gondola,tipo) => {
    let productList = null;
    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    const body = {
        "request_data": {
            "type_action": "get_all_by_g",
            "data": {
                "id_empresa_usuario_producto": "511095a8-da38-4acd-9254-90f82c75fb5b",
                "id_gondola": id_gondola,
                "nombre_campo_id": "id_gondola_"+tipo+"_producto"
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization": window.localStorage.getItem('adminToken')
        }
    }

    return axios
        .post(url + 'productos', body, {
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json"
            },
        })
        /* .then( response => {
            if(response.status == '200' && response.data.status != 'failed'){
                //obtengo el arreglo de productos
                
                productList = response.data?.body?.data || []
                
            }
        }
        ).catch( err => {
            console.log(err)
        }

        ) */

        

    

}