import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import background from "../../../assets/images/prev_estantes/Prev_Estante_1.jpg";


export default function CustomCard(props) {
    const [ancho, setAncho] = React.useState("0");
    const [alto, setAlto] = React.useState("0");

    React.useEffect(() => {
        switch (props.size) {
            case 'sm':
                setAncho(wds.sm)
                setAlto(hts.sm)
              break;
            case 'md':
                setAncho(wds.md)
                setAlto(hts.md)
              break;
            default:
              console.log("error");
          }
      },[]);

    const hts={
        sm:"5rem",
        md:"6.5rem"
    }

    const wds={
        sm:"4.5rem",
        md:"5.8rem"
    }

    let mainCard = {
        height: alto, 
        width: ancho, 
        backgroundColor:"white", 
        positon:"relative", 
        overflow:"hidden",
        marginRight:"0.5rem",
        borderRadius:"8px",
        boxShadow: "4px 4px 10px -5px black"
    }

    const roundNumber = {
        borderRadius:"50%",
        width:"1rem",
        height:"1rem",
        backgroundColor:"white",
        position:"absolute",
        top:"-0.5rem",
        left:"-0.5rem"
    }

    return(
        <Box sx={mainCard}>
            <button style={{width:"100%", height:"100%", padding:0, border:"none",backgroundColor:"transparent", cursor:"pointer", display:"flex",flexDirection:"column"}}
                onClick={()=>props.handleClick(props.number, props.arrobject)}>
                <Box sx={{position:"absolute", display:props.type===1?"none":"initial"}}>
                    <Box sx={roundNumber}>
                        <p style={{lineHeight:0.8,textAlign:"center", color:"black"}}>{props.number}</p>
                    </Box>
                </Box>
                
                <Box sx={{width:"100%", height:"20%", backgroundColor:"#ea660e", color:"white"}}>
                    {props.type===1?"n. "+props.number:"Local 1"}
                </Box>
                <Box sx={{width:"100%", height:`${props.type===1?"70%":"40%"}`, display:"flex", flexDirection:"column", alignItems:"center"}}>
                    {props.type===1?
                    <img src= {require("../../../assets/images/prev_estantes/"+props.image)} style={{width:"100%", height:"100%", objectFit:"scale-down"}}></img>
                    :<img src= {require("../../../assets/images/Locales/"+props.image)} style={{width:"90%", height:"100%", objectFit:"scale-down"}}></img>}
                    </Box>
                <Box sx={{width:"100%", height:"30%",display:`${props.type===2?"initial":"none"}`, fontSize:"0.8rem", textAlign:"center"}}>
                    Local tipo 1
                </Box>

            </button>
        </Box>
    )
}