/*  
*   Este componente es til para seguir a un elemento dado
*   Slo es necesario definir el id del target a seguir y un offset si se quiere aadir
*/

AFRAME.registerComponent('follow-entity', {
    schema: {
        target: {type: 'selector', default: null},
        offsetX: {type: 'number', default: 0},
        offsetY: {type: 'number', default: 0},
        offsetZ: {type: 'number', default: 0},
    },
    tick: function() { 
        // var player = document.querySelector("#Player");
        const target = this.data.target;
        
        if(target){
            // console.log('posX', this.el.object3D.position)
            this.el.object3D.position.set(target.object3D.position.x + this.data.offsetX, 
                target.object3D.position.y + this.data.offsetY, 
                target.object3D.position.z + this.data.offsetZ
            )
            
        }
        
    },
    
});