const AFRAME = window.AFRAME;
const THREE = window.THREE;

AFRAME.registerComponent('interactable-logo', {
  
    init: function() {
        let el = this.el;

        //Cdigo aqu********************************************************************
        el.addEventListener("click", function(){ 
            
            const ClickProductEvent = new CustomEvent('open_logo_modal', { 
                detail:{
                  element: el.getAttribute('src').charAt(0)=='#'?document.querySelector(el.getAttribute('src')):null/* document.querySelector(el.getAttribute('src'))?document.querySelector(el.getAttribute('src')):null */,
                  elImage: el
                } 
            });
            document.dispatchEvent(ClickProductEvent);
            
        });
        //Cdigo aqu********************************************************************

    },
  
  });

  AFRAME.registerComponent('interactable-product', {
  
    init: function() {
        let el = this.el;

        //Cdigo aqu********************************************************************
        el.addEventListener("click", function(){ 
            //console.log(el)
            const ClickProductEvent = new CustomEvent('open_product_modal', { 
                detail:{
                  element: el
                } 
            });
            document.dispatchEvent(ClickProductEvent);
            
        });
        //Cdigo aqu********************************************************************

    },
  
  });