import React from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { styled } from '@mui/material/styles';

//icons
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import {changeMat} from '../../../aframe-components/AdminComponents/changeMat';
import {changeTexture} from '../../../aframe-components/AdminComponents/changeMat';
import {changeLogo} from '../../../aframe-components/AdminComponents/changeMat';
import { updateLocales } from '../../../../server/UpdateLocal';


export default function MainLocal(props) {
    const [infoTabOpen, setInfoTabOpen] = React.useState(true);
    const [imageData, setImageData] = React.useState();

    React.useEffect(() => {
        let imagen = getLogo()
       setImageData(imagen)


    }, []);

    const getLogo = (local) =>{
        let logoimage = null
        let logopanel = document.getElementById('logo-Local1')
        logopanel.object3D.traverse(function (node) {
            if (node.isMesh) {
              
              //node.material = mat
              logoimage = node.material.map.image.currentSrc
            }
          })
        
    
        return logoimage
    }

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#ea660e",
        '&:hover': {
          backgroundColor: "#d65d0d",
        },
      }));

    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "15%",

        padding: '2rem',
    }

    const myStyle = {
        display: "flex",
        height: "100%",
        minWidth: "25%",
        padding: '1rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        minWidth: "50%",

        padding: '1rem'
    }
    const myStyle3 = {
        display: "flex",
        height: "100%",
        width: "33%",
        flexDirection: "column",
        padding: '1rem 1rem'
    }

    const myColorStyle = {
        display: "flex",
        height: "100%",
        width: "33%",
        padding: '0.5rem 1rem'
    }

    const contButton = {
        display: "flex",
        height: "100%",
        width: "10%",
        flexDirection: "column",
        justifyContent:"flex-end",
        padding: '0.5rem 1rem'
    }

    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }

    const colorContainer = {
        width: "100%",
        display:"flex",
        flexWrap:"wrap"
        
    }

    const textures = [
        "brick.jpg",
        "wood.jpg",
        "marble.jpg",
    ]

    const plt = [
        "#15133C",
        "#73777B",
        "#EC994B",
        "#F1EEE9",
        "#112B3C",
        "#205375",
        "#251D3A",
        "#2A2550",
        "#F66B0E",
        "#FF7700",
        "#EFEFEF",
        "#ea660e",
    ]

    React.useEffect(() => {

        if(!infoTabOpen){
            const image_input = document.getElementById("image-input");
            
            image_input.addEventListener("change", function() {
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                const uploaded_image = reader.result;
                
                
                setImageData(uploaded_image)
                changeLogo(uploaded_image)
                //console.log(uploaded_image)
                /* const imageDiv = document.querySelector("#display-image")
                imageDiv.style.backgroundImage = `url(${uploaded_image})`;
                imageDiv.style.display = 'inline-block' */
            });
            reader.readAsDataURL(this.files[0]);
            });
        }
        
    
      },[infoTabOpen]);

    function fillColors(){
        var rows = [];

        for (var i = 0; i < plt.length; i++) {
            let pos = i
            //No funciona el envo del arreglo en posicin i ***revisar
            rows.push(<button key={i} onClick={ function(){ changeMat(plt[pos])}}
            className="o-button-style" 
            style={{height:"1rem",width:"1rem",backgroundColor:plt[i]}}>
            </button>);
        }
        return <div style={colorContainer}>{rows}</div>;
    }

    function fillTextures(){
        var rows = [];

        for (var i = 0; i < textures.length; i++) {
            let pos = i
            //No funciona el envo del arreglo en posicin i ***revisar
            rows.push(<button key={i} onClick={ function(){ changeTexture(textures[pos])}}
            className="o-button-style" 
            style={{height:"1rem",width:"1rem"}}>
                <img src={require("../../../../assets/images/Locales/texturas/"+textures[i])} style={{ width: "100%", height: "100%", objectFit: "cover" }}></img>
            </button>);
        }
        return <div style={colorContainer}>{rows}</div>;
    }

    function changeTab(e) {
        //console.log(e)
        var tabInf = document.getElementById("tab-inf");
        var tabApa = document.getElementById("tab-apa");
        //console.log(e.target.id)

        if (e.target.id === "btn-inf") {
            tabInf.classList.add("o-tab-active")
            tabApa.classList.remove("o-tab-active")
            setInfoTabOpen(true)
        } if (e.target.id === "btn-apa") {
            tabInf.classList.remove("o-tab-active")
            tabApa.classList.add("o-tab-active")
            setInfoTabOpen(false)
        }

    }

    const handleSave = () => {
        //console.log(props.arrobject)
        updateLocales("Local1")
        
    };


    return (
        <Box sx={{ width: '90%', height: '80%', backgroundColor: '#676767', display: 'flex' }}>

            {/* Pestaas superiores */}
            <Box sx={{ width: "auto", height: "1.8rem", position: "absolute", top: "0", display: "flex" }}>
                <Box id="tab-inf" className="o-tab-active" sx={pestanaStyle}><Button sx={{ padding: 0 }} variant="text" onClick={changeTab} id="btn-inf">Informacin</Button></Box>
                <Box id="tab-apa" className="" sx={pestanaStyle}><Button variant="text" sx={{ padding: 0 }} onClick={changeTab} id="btn-apa">Apariencia</Button></Box>
            </Box>

            <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
                <Box sx={backDivStyle}>
                    <button style={styleButton} onClick={props.handleClickBack}>
                        <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
                        <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
                    </button>


                </Box>
                <Divider orientation="vertical" variant="middle" flexItem />
                {infoTabOpen &&
                    <>
                        <Box sx={myStyle}>
                            <Box sx={{ width: "40%", height: "70%" }}>
                                <img src={require("../../../../assets/images/Locales/Local2_1.jpg")} style={{ width: "100%", height: "100%", objectFit: "scale-down" }}></img>
                            </Box>
                            <Box>
                                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Local tipo 1</Typography>
                                <Typography sx={{ color: 'white' }} variant='h3'>#1</Typography>
                            </Box>

                        </Box>
                        <Divider orientation="vertical" variant="middle" flexItem />
                        <Box sx={myStyle2}>
                            <Box sx={{ width: "30%" }}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Nombre</Typography>
                                <input className="o-input" type="text"></input>
                            </Box>
                            <Box sx={{ width: "45%", marginLeft: "0.5rem" }}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Descripcin</Typography>
                                <input className="o-input" type="text" style={{ height: "3rem", width: "100%" }}></input>
                            </Box>

                        </Box>
                    </>
                }

                {!infoTabOpen &&
                    <>
                        <Box sx={myStyle3}>
                            <Box sx={{ width: "100%" }}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Logo</Typography>

                            </Box>
                            <Box sx={{ width: "100%", height: "50%", display: "flex" }}>
                                <Box sx={{ width: "50%", height: "100%", backgroundColor: "white" }}>
                                    <img src= {imageData} style={{width:"100%", height:"100%", objectFit:"scale-down"}}></img>
                                </Box>
                                <Box>
                                    <button className="o-button-style custom-input-file" style={{ display: "flex", height:"auto", marginLeft: "0.2rem" }}>
                                        <FolderRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                        <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Elegir archivo</Typography>
                                        <input type="file" id="image-input" className="input-file" accept="image/jpeg, image/png, image/jpg">
                                        </input>
                                    </button>

                                    <Box sx={{ display: "flex", marginTop: "0.5rem", marginLeft: "0.2rem" }}>
                                        <button className="o-button-style" style={{ display: "flex", height:"auto", marginLeft: "0.2rem" }} 
                                            onClick={()=>{
                                                setImageData("")
                                                changeLogo("")
                                            }}>
                                            <DeleteRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                            <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Eliminar logo</Typography>
                                        </button>
                                        
                                    </Box>
                                </Box>
                            </Box>

                        </Box>
                        <Divider orientation="vertical" variant="middle" flexItem />
                        <Box sx={myColorStyle}>
                            <Box sx={{marginRight:"1rem", width:"50%"}}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Color del local</Typography>
                                {fillColors()}
                            </Box>
                            <Box>
                                <Typography sx={{ color: 'white' }} variant='h6'>Textura del local</Typography>
                                {fillTextures()}
                            </Box>
                        </Box>
                        <Box sx={contButton}>
                            <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#ea660e", height:"3rem" }}>
                                Guardar
                            </ColorButton> 
                        </Box>
                    </>
                }

            </Box>

        </Box>
    )
}