import React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import CategoryIcon from '@mui/icons-material/Category';
import { useTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

//  COMPONENTES UI
// import ShoppingCart from './ShoppingCart';

export default function ProductModal() {
  const [open, setOpen] = React.useState(false);
  const [panelEl, setPanelEl] = React.useState();
  const [panelElimg, setPanelElimg] = React.useState();
  const inputEl = React.useRef(null);
  const [imageData, setImageData] = React.useState();
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

  React.useEffect(() => {
    console.log("use effect modal")
    document.addEventListener('open_logo_modal', function (e) {
      //console.log(e.detail.element);
      setPanelEl(e.detail.element)
      setPanelElimg(e.detail.elImage)

      if(e.detail.element){
        setImageData(e.detail.element.getAttribute('src'))
      }else{
        setImageData(e.detail.elImage.getAttribute('src'))
      }

      
      handleClickOpen();
    })


  }, []);

  function changeInput(e){
    
        const reader = new FileReader();
        
        reader.addEventListener("load", () => {
            const uploaded_image = reader.result;
            
            
            setImageData(uploaded_image)
            
            //changeLogo(uploaded_image)
            //console.log(uploaded_image)
            
        });
        reader.readAsDataURL(e.target.files[0]);
  }
        
    
    

  

  const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
      padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
      padding: theme.spacing(1),
    },
  }));

  const BootstrapDialogTitle = (props) => {
    const { children, onClose, ...other } = props;

    return (
      <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
        {children}
        {onClose ? (
          <IconButton
            aria-label="close"
            onClick={onClose}
            sx={{
              position: 'absolute',
              right: 8,
              top: 8,
              color: (theme) => theme.palette.grey[500],
            }}
          >
            <CloseIcon />
          </IconButton>
        ) : null}
      </DialogTitle>
    );
  };

  BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
  };

  const ColorButton = styled(Button)(({ theme }) => ({
    color: "white",
    backgroundColor: "#ea660e",
    '&:hover': {
      backgroundColor: "#d65d0d",
    },
  }));

  const contentStyle = {
    minHeight: "10rem",
    width: "100%",
    display: "flex",
    justifyContent: "space-around"
  }


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSave = () => {
    setOpen(false);
    //console.log(panelEl.getAttribute('src'))
    //document.querySelector(panelEl.getAttribute('src')).setAttribute("src",imageData)
    //.setAttribute("src",imageData)
    //panelEl.setAttribute('src',imageData)
    //panelElimg.removeAttribute('src')
    panelElimg.setAttribute('src',imageData)
    
  }

  return (
    <div>
      {/* <Button variant="outlined" className='interactable-ui' onClick={handleClickOpen}>
        Open responsive dialog
      </Button> */}
      {/* <ShoppingCart/> */}
      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
        fullWidth={true}
        maxWidth='sm'
      >
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
          Cambiar logo N
        </BootstrapDialogTitle>
        <DialogContent dividers sx={contentStyle}>
          <Box sx={{ width: "60%", height: "160px", backgroundColor: "gray" }}>
            <img src={imageData} style={{ width: "100%", height: "100%", objectFit: "contain" }}></img>
          </Box>
          <Box>
            <button className="o-button-style custom-input-file" style={{ display: "flex", height: "auto", marginLeft: "0.2rem" }}>
              <FolderRoundedIcon sx={{ mr: 1, color: "gray", fontSize: "1.2rem" }} />
              <Typography sx={{ color: 'gray', fontSize: "1.2rem" }}>Elegir archivo</Typography>
              <input ref={inputEl} onChange={changeInput} type="file" id="image-modal-input" className="input-file" accept="image/jpeg, image/png, image/jpg">
              </input>
            </button>


            <button className="o-button-style" style={{ display: "flex", height: "auto", marginLeft: "0.2rem" }}
              onClick={() => {
                  setImageData(require('../../../assets/images/logos_loacles/addLogo.jpg'))
              }}>
              <DeleteRoundedIcon sx={{ mr: 1, color: "gray", fontSize: "1.2rem" }} />
              <Typography sx={{ color: 'gray', fontSize: "1.2rem" }}>Eliminar logo</Typography>
            </button>


          </Box>
        </DialogContent>
        <DialogActions>
          <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#ea660e" }}>
            Aplicar
          </ColorButton>
        </DialogActions>
      </BootstrapDialog>
    </div>
  );
}