import axios from "axios";
import { getLocales } from "./DataLocal";
import { getPubli } from "./DataPubli";

//  Ingreso con un usuario genrico de hoyostools para poder hacer las consultas
export const loginEventVR = () => {

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
    
    const bodyLogin = {
        "request_data": {
        "type_action": "login",
            "data": {
                "correo_usuario": "gerente3@hoyostools.com",
                "clave_usuario": "123456"
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json"
        }
    }

    // Axios
    axios.post(url + 'login', bodyLogin, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
    .then(async(response) => {
        console.log('respuesta :', response.data.status)
       
        if(response.status == '200' && response.data.status != 'failed'){
            
            let EventVRToken = response.data.result.token;
            let idEmpresa = response.data.result.user_data.id_empresa_usuario;
            window.localStorage.setItem('adminToken', EventVRToken);
            //console.log('idempresa: ', response.data.result)
            getLocales(EventVRToken, idEmpresa)
            getPubli(EventVRToken)

            //Obtener gndolas tipo 1***************
            try {
                const responseGondolas_1 = await axios.post(url + 'gondolas', {
                    "request_data": {
                        "type_action": "get_all",
                        "data": {
                        "id_local_gondola_tipo_1": "b157ce22-412c-4bd9-8cd2-fc22699e0c59" //Cambio aqu luego con el id del local seleccionado
                        }
                    },
                    "headers": {
                        "Content-Type": "application/json",
                        "Accept": "application/json",
                        "Authorization": EventVRToken
                    }
                });

                if(responseGondolas_1.status == '200' && responseGondolas_1.data.status != 'failed'){
                    
                    const gondolasArray = responseGondolas_1.data.body.data;
                   // console.log('gndolas',gondolasArray );
                   window.localStorage.setItem('arrayGondolas1', JSON.stringify(gondolasArray));

                   const LoadProductEvent = new CustomEvent('load_shelves_type_1', { 
                    detail:{
                        //shelvesDataArray: JSON.stringify(gondolasArray)
                        shelvesDataArray: gondolasArray
                    } 
                    });
                    document.dispatchEvent(LoadProductEvent);
                   
                }
            }
            catch (err) {
                console.error(err);
            }
            //Obtener gndolas tipo 1***************

            //Obtener gndolas tipo 2************************************

            try {
                const responseGondolas_2 = await axios.post(url + 'gondolas', {
                    "request_data": {
                        "type_action": "get_all_g2",
                        "data": {
                        "id_local_gondola_tipo_2": "b157ce22-412c-4bd9-8cd2-fc22699e0c59" //Cambio aqu luego con el id del local seleccionado
                        }
                    },
                    "headers": {
                        "Content-Type": "application/json",
                        "Accept": "application/json",
                        "Authorization": EventVRToken
                    }
                });

                if(responseGondolas_2.status == '200' && responseGondolas_2.data.status != 'failed'){
                    
                    const gondolas2Array = responseGondolas_2.data.body.data;
                   // console.log('gndolas',gondolasArray );
                   window.localStorage.setItem('arrayGondolas2', JSON.stringify(gondolas2Array));

                   const LoadProductEvent2 = new CustomEvent('load_shelves_type_2', { 
                    detail:{
                        //shelvesDataArray: JSON.stringify(gondolasArray)
                        shelvesDataArray: gondolas2Array
                    } 
                    });
                    document.dispatchEvent(LoadProductEvent2);
                   
                }
            }
            catch (err) {
                console.error(err);
            }

            //Obtener gndolas tipo 2************************************

            //OBTEMER GNDOLAS TIPO 3************************************
            try {
                const responseGondolas_3 = await axios.post(url + 'gondolas', {
                    "request_data": {
                        "type_action": "get_all_g3",
                        "data": {
                        "id_local_gondola_tipo_3": "b157ce22-412c-4bd9-8cd2-fc22699e0c59" //Cambio aqu luego con el id del local seleccionado
                        }
                    },
                    "headers": {
                        "Content-Type": "application/json",
                        "Accept": "application/json",
                        "Authorization": EventVRToken
                    }
                });

                if(responseGondolas_3.status == '200' && responseGondolas_3.data.status != 'failed'){
                    
                    const gondolas3Array = responseGondolas_3.data.body.data;
                   // console.log('gndolas',gondolasArray );
                   window.localStorage.setItem('arrayGondolas3', JSON.stringify(gondolas3Array));

                   const LoadProductEvent3 = new CustomEvent('load_shelves_type_3', { 
                    detail:{
                        //shelvesDataArray: JSON.stringify(gondolasArray)
                        shelvesDataArray: gondolas3Array
                    } 
                    });
                    document.dispatchEvent(LoadProductEvent3);
                   
                }
            }
            catch (err) {
                console.error(err);
            }

        }
    })
    .catch(error => {
        console.log('error : ', error)
    });
}