import axios from "axios";
import { setInitPubli } from "../components/aframe-components/AdminComponents/changeMat";

export const getPubli = (token) =>{

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
    
    const body ={
        "request_data": {
          "type_action": "get_all_pu",
          "data": {
            "id_marketplace": "aa7b8c56-959a-435b-9b20-c2f9b2dbd899"
          }
        },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization": token
      }
    }

    // Axios
    axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'marketplaces', body, {
      headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
      },
    })
    .then((response) => {
        

      if(response.status == '200' && response.data.status != 'failed'){
        const dataPubli = response.data.body.data;
        //console.log(dataPubli)
        window.localStorage.setItem('arrpubli', JSON.stringify(dataPubli));
        setInitPubli(dataPubli)

        
      }
    })
    .catch(error => {
        console.error('error : ', error)
    });

  }