import React, { useEffect, useState } from 'react';

//  Componentes MUI
import { Box, Button, Drawer, Typography } from '@mui/material';

export default function DrawerLocalInfo() {
  const [open, setOpen] = useState(false);

  const toggleDrawer = ( open ) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setOpen(open);
  };

  useEffect( () => {

    //  Se escucha al evento de colisin con un teleporter de aframe para abrir el drawer
    document.addEventListener('open_local_info', toggleDrawer(true), false);

    //  Se escucha al evento de colisin con un teleporter de aframe para cerrar el drawer
    document.addEventListener('close_local_info', toggleDrawer(false), false);

    //  Remover el eventlistener al desmontarse el componente
    return () => {
        document.removeEventListener('open_local_info', toggleDrawer);
        document.removeEventListener('open_local_info', toggleDrawer);
    }

  }, []);


  const handleEntry = () => {
    // REDIRIGIR AL LOCAL
  }

  return (
    <div>
        <Drawer
            anchor={'right'}
            open={open}
            onClose={toggleDrawer(false)}
          >
            <Box
                sx={{ width: 250, height: '100%', m:3, textAlign:'center', display:'flex', flexFlow:'column', justifyContent:'center', alignItems:'center' }}
                role="presentation"
                onClick={toggleDrawer(false)}
                onKeyDown={toggleDrawer( false)}
                >
                    <Typography  variant="h5" gutterBottom component="div">
                        Nombre del local
                    </Typography>
                    <Typography  variant="button" gutterBottom component="div">
                        Lorem Ipsum is simply dummy text of the printing and typesetting industry. 
                        Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
                         when an unknown printer took a galley of type and scrambled it to make a 
                         type specimen book.
                    </Typography>
                    
                    <Button variant="outlined" sx={{mt:3}} onClick={handleEntry}>Entrar</Button>
            </Box>
            
        </Drawer>
    </div>
  );
}