import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';




import GeneralTab from './GeneralMall';
import PublicidadTab from './PublicidadMall';

export default function ConfigMall() {
    const [generalTabOpen, setGeneralTabOpen] = React.useState(true);

    const pestanaStyle={
        width:"7rem",
        height:"100%",
        backgroundColor:"lightGray",
        marginRight:"0.5rem",
        borderRadius: "0px 0px 5px 5px" 
    }

    function changeTab(e){
        //console.log(e)
        var tabGen = document.getElementById("tab-gen");
        var tabPubli = document.getElementById("tab-publi");
        //console.log(e.target.id)

        if(e.target.id==="btn-gen"){
            tabGen.classList.add("o-tab-active")
            tabPubli.classList.remove("o-tab-active")
            setGeneralTabOpen(true)
        }if(e.target.id==="btn-publi"){
            tabGen.classList.remove("o-tab-active")
            tabPubli.classList.add("o-tab-active")
            setGeneralTabOpen(false)
        }   
        
    }
    return(
        <Box sx={{width:"100%", height:"100%", display:"flex"}}>
                        <Box sx={{width:'15%', height:'100%', display:'flex', alignItems:'center', justifyContent:'center'}}>
                            <Typography sx={{textAlign:'center',color:'white'}} variant='h6'>Configuracin centro comercial</Typography>
                        </Box>
                        <Divider orientation="vertical" variant="middle" flexItem />
    
                        {/* Dividir en componentes */}
                        <Box sx={{width:'85%', height:'100%', display:'flex', alignItems:'center', justifyContent:'center'}}>
                        
                            <Box sx={{width:'90%', height:'80%',backgroundColor: '#676767', display:'flex'}}>
    
                                {/* Pestaas superiores */}
                                <Box sx={{width:"auto",height:"1.8rem", position:"absolute", top:"0", display:"flex"}}>
                                        <Box id="tab-gen" className="o-tab-active" sx={pestanaStyle}><Button sx={{padding:0}} variant="text" onClick={changeTab} id="btn-gen">General</Button></Box>
                                        <Box id="tab-publi" className="" sx={pestanaStyle}><Button variant="text" sx={{padding:0}} onClick={changeTab} id="btn-publi">Publicidad</Button></Box>
                                </Box>
    
                                {generalTabOpen &&
                                    <GeneralTab></GeneralTab>
                                }
                                {!generalTabOpen &&
                                    <PublicidadTab></PublicidadTab>
                                }
                                
    
                                
                                
                            </Box>
                        </Box>
                    </Box>
    )
}