import React from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';

//icons
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import { getMultipleProducts } from '../../../../server/Data';
import { getProducts } from '../../../../server/DataProducts';
import { updateProduct } from '../../../../server/UpdateProduct';
import { reload_shelf } from '../../../scripts/EstanteriasAdmin';

import * as XLSX from 'xlsx/xlsx.mjs';


export default function MainProductos(props) {

  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen = () => {
    setOpen(true);
  };


  const styleButton = {
    height: "fit-content",
    padding: 0,
    border: "none",
    backgroundColor: "transparent",
    cursor: "pointer",
    display: "flex",

  }

  const backDivStyle = {
    display: "flex",
    height: "100%",
    width: "13%",

    padding: '2rem 0.5rem',
  }

  const myStyle = {
    display: "flex",
    height: "100%",
    width: "auto",
    padding: '0rem 0.5rem',
    flexDirection:'column',
    justifyContent: "center",
    alignItems: "center"

  }

  const myStyle2 = {
    display: "flex",
    height: "100%",
    width: "33%",
    flexDirection: "column",
    padding: '0.5rem 1rem'
  }

  const pestanaStyle = {
    width: "7rem",
    height: "100%",
    backgroundColor: "lightGray",
    marginRight: "0.5rem",
    borderRadius: "0px 0px 5px 5px"
  }

  React.useEffect(() => {

    var selectedFile;
    document
      .getElementById("fileUpload")
      .addEventListener("change", function (event) {
        selectedFile = event.target.files[0];
      });
    document
      .getElementById("uploadExcel")
      .addEventListener("click", function () {
        if (selectedFile) {
          console.log("Upload Excel");
          var fileReader = new FileReader();
          fileReader.onload = function (event) {
            var data = event.target.result;

            var workbook = XLSX.read(data, {
              type: "binary"
            });
            workbook.SheetNames.forEach(sheet => {
              let rowObject = XLSX.utils.sheet_to_row_object_array(
                workbook.Sheets[sheet]
              );
              let jsonObject = JSON.stringify(rowObject);
              //document.getElementById("jsonData").innerHTML = jsonObject;
              //console.log(jsonObject);
              //console.log(rowObject)
              agregarProductos(rowObject)

            });

          };
          fileReader.readAsBinaryString(selectedFile);
        }
      });



  }, []);


  function agregarProductos(list) {
    let codes = ""
    let tipoRecibido
    list.forEach(element => {
      if (element.Articulo) {
        codes = codes + "," + element.Articulo
      }
      tipoRecibido=element.Tipo

    });

    if(tipoRecibido == props.tipo){
      let listproducts = getMultipleProducts(codes)
    listproducts.then((response) => {
      console.log("respuesta imagen", response);
      var size = Object.keys(response.result).length;
      console.log(size)
      let responseList = response.result
      if (response.error === false) {
        //let dataProductResponse = response.result[product_code]; 


        let aux = getProducts(props.idGondola, "tipo" + props.tipo)
        aux.then(response => {
          if (response.status == '200' && response.data.status != 'failed') {
            //obtengo el arreglo de productos

            let productList = response.data?.body?.data || []
            actualizarProductos(productList, responseList, list)
            //console.log(productList)

          }
        }
        ).catch(err => {
          console.log(err)
        }

        )
      }
      else {
        alert(response.message)
        //console.log("error2")
      }

    })
      .catch(error => console.error('Error:', error));

    }else{
      alert("El tipo de Gndola del archivo no coincide")
    }
    
  }


  function actualizarProductos(productList, imageList, productExcel){
    handleOpen()
    printFiles(productList, imageList, productExcel)

   /*  productList.forEach((element,index) => {
      if(productExcel[index]){
        if(productExcel[index].Articulo){
          let code = productExcel[index].Articulo
          let imagen = imageList[productExcel[index].Articulo]

          if(imagen){
            
            //espere
              toDataURL(
                imagen,
                code,
                element,
                function (dataURL, codigo, obj) {
                    //imagen2 = dataURL
                  //console.log(codigo)

                  //esperar
                   updateProduct(obj, dataURL, codigo, true)
                },
              )
            
            
          }
          
          

          //Actualizar producto
        }
      }
    }); */ 
  }


  async function printFiles (productList, imageList, productExcel) {
    
    let index = 0
    for (const element of productList) {
      if(productExcel[index]){
        if(productExcel[index].Articulo){
          let code = productExcel[index].Articulo
          let imagen = imageList[productExcel[index].Articulo]

          if(imagen){
       
            var x = await toDataURL(imagen, code, element)
            //console.log(code)
            await updateProduct(x.obj, x.dataURL, x.code, true)
            //console.log(x)
          }
         
        }
      }
      
      
      
      index++
    }
    console.log("finish images")
    handleClose()
    reload_shelf(props.idGondola)
    alert("Se ha completado la carga")
  }

  function toDataURL(src, code, obj) {


    return new Promise(resolve => {
      var image = new Image()
    image.crossOrigin = 'Anonymous'

    image.onload = function () {
      var canvas = document.createElement('canvas')
      var context = canvas.getContext('2d')
      canvas.height = this.naturalHeight
      canvas.width = this.naturalWidth
      context.drawImage(this, 0, 0)
      var dataURL = canvas.toDataURL('image/jpeg')

      let object = {
        dataURL,
        code,
        obj
      }
      resolve(object)
    }
    image.src = src
    });
  }


  return (


    <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
      <Box sx={backDivStyle}>
        <button style={styleButton} onClick={() => props.handleClickBack(props.arrobject.clave_gondola_tipo_1)}>
          <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
          <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
        </button>


      </Box>
      <Divider orientation="vertical" variant="middle" flexItem />
      <Box sx={myStyle}>
        {/* No tienes productos configurados
                <AddCircleOutlineRoundedIcon sx={{ mr: 1, color: "white" }} /> */}
        {/* <Typography sx={{ color: 'white' }}>Lista de productos</Typography> */}
        <input type="file" id="fileUpload" accept=".xls,.xlsx" /><br />
        <button type="button" id="uploadExcel">Subir</button>
        <pre id="jsonData"></pre>

      </Box>


      {/* CARGA */}

      <Backdrop
        sx={{ display:"flex",flexDirection:"column",color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={open}
      >
        <CircularProgress color="inherit" />
        <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Cargando productos...</Typography>
      </Backdrop>

    </Box>


  )
}