import React, { useEffect } from 'react';

//  Componentes
import PlaneImageProduct from './PlaneProduct';
//import ImageHoyos from '../../../assets/images/logos_loacles/hoyostools.png';
import ImageHoyos from '../../../assets/images/logos_loacles/addLogo.jpg';
import ImageHoyos2 from '../../../assets/images/logos_loacles/logo1.png'
import addLogo from '../../../assets/images/logos_loacles/addLogo.jpg'

import { getProducts } from '../../../server/DataProducts';

export default function Shelf3D1({ keyN, position, rotation, color1, color2, logos, id }) {
    const [visibleLogos, setVisibleLogos] = React.useState(false);
    const [visibleProducts, setVisibleProducts] = React.useState(false);

    const [productsLoaded, setProductsLoaded] = React.useState(false);
    const [firstL, setFirstL] = React.useState(false);
    const [productsList, setProductsList] = React.useState([]);


    const url = "https://d3iqhfs7sfm8r5.cloudfront.net/"

    React.useEffect(() => {

        document.addEventListener('load_shelf', function (e) {
            
            if (e.detail.idShelf === keyN) {
                //console.log(logos.superior1);
                if (e.detail.dataVisible === 'Products') {
                    setVisibleProducts(true)
                    setVisibleLogos(false)
                    setFirstL(true)
                    

                } else {
                    setVisibleProducts(false)
                    setVisibleLogos(true)
                    //getProducts(id)
                }
            }
        })

        document.addEventListener('leave_shelf', function (e) {
            
            if (e.detail.idShelf === keyN) {
                setVisibleProducts(false)
                setVisibleLogos(false)
                //console.log(logos.superior1);
            }
        })

        /**Reload shelf
         * if id == keyn
         * setproductsloaded false
         * call new function to getproducts
         * setproductsloaded true
         * 
         */

         document.addEventListener('reload_shelf', function (e) {
            
           
            if (e.detail.idShelf === id) {
                console.log("reload shelf")
                setProductsLoaded(false)
                getP()
                setProductsLoaded(false)
                
            }
        })


    }, []);

    React.useEffect(() => {

        if(firstL){
            getP()
        }

    },[firstL])

    function getP(){
        let aux = getProducts(id, "tipo1")
        aux.then( response => {
            if(response.status == '200' && response.data.status != 'failed'){
                //obtengo el arreglo de productos
                
                let productList = response.data?.body?.data || []
                console.log(productList)
                setProductsLoaded(true)
                setProductsList(productList)
                
            }
        }
        ).catch( err => {
            console.log(err)
        }

        )
    }

    

    function loadProducts (){
        let rows = [];
        let ubi
        let image

        productsList.forEach(element => {
            ubi = JSON.parse(element.posicion_producto)
            image = element.imagen_producto

            rows.push(
            <PlaneImageProduct
                        position={ubi.position.x+" "+ubi.position.y+" "+ubi.position.z}
                        rotation={ubi.rotation.x+" "+ubi.rotation.y+" "+ubi.rotation.z}
                        width={ubi.width} 
                        height={ubi.height}
                        image={image=='imagen_producto'?ImageHoyos:url+image}
                        name={image=='imagen_producto'?"Producto no asignado":"Producto asignado"} //cambiar por nombre
                        vis={visibleProducts}
                        id={element.id_producto}
                        arrobj={JSON.stringify(element)} 
                        key={"k"+element.id_producto} />)
        });

        return <a-entity id="products-container">{rows}</a-entity>;
    }

    return (
        <a-entity id={"shelf-" + keyN + "-container"} scale="1 1 1" position={position} rotation={rotation}>

            {/* Texto con el id */}
            <a-entity position="0 3.5 0" scale="2 2 2" visible="true"
                text={"value:" + keyN + "; align:center; font:mozillavr; width:0.5; wrapCount:3"}
                look-at="#player-camera">
            </a-entity>

            <a-entity
                id={"shelf" + keyN}
                className="shelf-1-model"
                scale="50 50 50"
                gltf-model="#model-asset-shelf-1"
                ammo-body="type: static; "
                ammo-shape="type: box; fit:manual; halfExtents: 0.8 2 2.7"
                initial-materials={"colorCuerpo: "+color1+"; colorBorde: "+color2}>

            </a-entity>

            
                    <a-image
                        id={"superior1-"+keyN}
                        src={logos.superior1=='logo_superior1_gondola'?addLogo:url+logos.superior1} width="1.1" height="0.46"
                        //src2={logos.superior1=='logo_superior1_gondola'?addLogo:url+logos.superior1}
                        adjust-image="maxW:1.1; maxH:0.46"
                        position="0.04214 2.49915 1.07323" rotation="0 90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}
                        crossorigin="anonymous" >
                    </a-image>

                    <a-image
                    id={"superior2-"+keyN} 
                        src={logos.superior2=='logo_superior2_gondola'?addLogo:url+logos.superior2} width="1.1" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="0.04214 2.49915 -1.072" rotation="0 90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image>

                    <a-image id={"superior3-"+keyN}
                    src={logos.superior3=='logo_superior3_gondola'?addLogo:url+logos.superior3} width="1.1" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="-0.029 2.49915 -1.072" rotation="0 -90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image>

                    <a-image id={"superior4-"+keyN}
                    src={logos.superior4=='logo_superior4_gondola'?addLogo:url+logos.superior4} width="1.1" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="-0.029 2.49915 1.07323" rotation="0 -90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image>

                    {/* Banner inferior */}

                    {/* <a-image
                        id={"inferior1-"+keyN}
                        src={addLogo} width="2.0" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="0.717 0.222 1.07323" rotation="-11.801 90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image>

                    <a-image
                    id={"inferior2-"+keyN}
                        src={addLogo} width="2.0" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="0.717 0.222 -1.072" rotation="-11.801 90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image>

                    <a-image
                    id={"inferior1-"+keyN}
                        src={addLogo} width="2.0" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="-0.717 0.222 1.07323" rotation="-11.801 -90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image>

                    <a-image
                    id={"inferior2-"+keyN}
                        src={addLogo} width="2.0" height="0.46"
                        adjust-image="maxH:1.1; maxW:0.46"
                        position="-0.717 0.222 -1.072" rotation="-11.801 -90 0"
                        class={visibleLogos?"clickable product-entity":""}
                        interactable-logo
                        visible={visibleLogos}>
                    </a-image> */}
             


            {/* Collider para activar productos */}
            <a-sphere
                color="green"
                visible="false"
                ammo-body="
                        type: static; 
                        emitCollisionEvents:true; 
                        disableCollision:true"
                ammo-shape="type:sphere"
                radius='5'
                lod-shelf="selectorToHide: #products-container;">

            </a-sphere>

            {/* Productos */}

            {productsLoaded &&
                loadProducts()
            }

            


        </a-entity>
    );
}