

export function config_shelf(id, visible){
    const myEvent1 = new CustomEvent('load_shelf', { 
        detail:{
          idShelf: id,
          dataVisible: visible
        } 
    });
    document.dispatchEvent(myEvent1);
}

export function leave_shelf(id){
  const myEvent1 = new CustomEvent('leave_shelf', { 
      detail:{
        idShelf: id,
      } 
  });
  document.dispatchEvent(myEvent1);
}

export function reload_shelf(id){
  const myEvent1 = new CustomEvent('reload_shelf', { 
      detail:{
        idShelf: id
      } 
  });
  document.dispatchEvent(myEvent1);
}