/**
 * Plantilla de React para inicializar las vistas principales (Login, Personalizacin, Experiencia...)
 * Recibe como prop el hijo del componente que ser envuelto en los providers de contexto y temas
 */

import React from 'react';

// Temas y colores de la app
import { CssBaseline } from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';
import {theme , defaultTheme} from '../../theme/index';

//Provider de datos del usuario
import { UserDataProvider } from '../../context/UserDataContext';

function ContextTemplate({ children }) {
  return (
    // Envuelve el componente 'children' dentro del proveedor de tema y el contexto de la pgina
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <UserDataProvider>

        { children }

      </UserDataProvider>
      
    </ThemeProvider>
    
  );
}

export default ContextTemplate;
