//  Creacin de materiales

//  vidrio
const vidrioMaterial = new THREE.MeshStandardMaterial({
    transparent: true,
    opacity: 0.8,
    color: "#d6d6d6", 
    roughness: 0.2,
    metalness:0.1,
});


//  ladrillos
const ladrilloTexture = new THREE.TextureLoader().load(require(
    '../../../assets/models/mall/Texturas/brick-wall-185086.jpg') );
ladrilloTexture.wrapS = THREE.RepeatWrapping;
ladrilloTexture.wrapT = THREE.RepeatWrapping;

const ladrilloMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    map: ladrilloTexture,
});

//  Tierra
const tierraTexture = new THREE.TextureLoader().load( 
    require('../../../assets/models/mall/Texturas/Tierra/forrest_ground_01_diff_1k.jpg' ));
tierraTexture.wrapS = THREE.RepeatWrapping;
tierraTexture.wrapT = THREE.RepeatWrapping;

const tierraNormalTexture = new THREE.TextureLoader().load( 
    require('../../../assets/models/mall/Texturas/Tierra/forrest_ground_01_nor_dx_1k.jpg') );
tierraNormalTexture.wrapS = THREE.RepeatWrapping;
tierraNormalTexture.wrapT = THREE.RepeatWrapping;

const tierraMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    map: tierraTexture,
    normalMap: tierraNormalTexture
});

//  Arbusto
const arbustoTexture = new THREE.TextureLoader().load( 
    require('../../../assets/models/mall/Texturas/Bush.jpg') );
arbustoTexture.wrapS = THREE.RepeatWrapping;
arbustoTexture.wrapT = THREE.RepeatWrapping;

const arbustoMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    map: arbustoTexture,
});


//  Suelo
const sueloTexture = new THREE.TextureLoader().load( 
    require('../../../assets/models/mall/Texturas/Floor/marble_01_diff_1k.jpg' ));
sueloTexture.wrapS = THREE.RepeatWrapping;
sueloTexture.wrapT = THREE.RepeatWrapping;

const sueloNormalTexture = new THREE.TextureLoader().load( 
    require('../../../assets/models/mall/Texturas/Floor/marble_01_nor_dx_1k.jpg' ));
sueloNormalTexture.wrapS = THREE.RepeatWrapping;
sueloNormalTexture.wrapT = THREE.RepeatWrapping;

// const sueloAOTexture = new THREE.TextureLoader().load( 'Assets/3DModels/Mall/Texturas/Floor/marble_01_ao_1k.jpg' );
// sueloAOTexture.wrapS = THREE.RepeatWrapping;
// sueloAOTexture.wrapT = THREE.RepeatWrapping;

const sueloMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    map: sueloTexture,
    normalMap: sueloNormalTexture,
    aoMap: null
});


//  Madera silla
const maderaTexture = new THREE.TextureLoader().load( 
    require('../../../assets/models/mall/Texturas/wood-2148495_1920.jpg') );
arbustoTexture.wrapS = THREE.RepeatWrapping;
arbustoTexture.wrapT = THREE.RepeatWrapping;

const maderaMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    map: maderaTexture,
});

const aguaMaterial = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    color: '#7AD2F7'
});


AFRAME.registerComponent('mall-materials', {
    init: function(){
        let el = this.el;

        el.addEventListener("model-loaded", e =>{
            let mesh = el.getObject3D('mesh');

            
            if (!mesh){return;}   
            mesh.traverse(function(node){
                
                if(node.name === 'Piso1_0'){
                    console.log(node.position)
                }

                if (node.isMesh){  


                    switch(node.material.name) {
                        case "Vidrio_Color":
                            node.material = vidrioMaterial;
                            break;
                        case "Brick_Color":
                            node.material = ladrilloMaterial;
                            break;
                        case "Tierra_Color":
                            node.material = tierraMaterial;
                            break;
                        case "Arbusto":
                            node.material = arbustoMaterial;
                            break;
                        case "Suelo_Color":
                            node.material = sueloMaterial;
                            break;
                        case "Hojas_Palma_color":
                            node.material.side = THREE.DoubleSide;
                            break;
                        case "MaderaSilla_Color":
                            node.material = maderaMaterial;
                            break;
                        case "Agua":
                            const verts = node.geometry.vertices;
                            node.material = aguaMaterial;
                            break;

                    }
                }
            });


        });
    },
  
});


AFRAME.registerGeometry('custom-gemetry', {
    schema: {
      vertices: {
        default: ['-10 10 0', '-10 -10 0', '10 -10 0'],
      }
    },
  
    init: function (data) {
      var geometry = new THREE.Geometry();
      geometry.vertices = data.vertices.map(function (vertex) {
          var points = vertex.split(' ').map(function(x){return parseInt(x);});
          return new THREE.Vector3(points[0], points[1], points[2]);
      });
      geometry.computeBoundingBox();
      geometry.faces.push(new THREE.Face3(0, 1, 2));
      geometry.mergeVertices();
      geometry.computeFaceNormals();
      geometry.computeVertexNormals();
      this.geometry = geometry;
    }
});

