const AFRAME = window.AFRAME;
const THREE = window.THREE;

AFRAME.registerComponent('initial-materials', {
  schema: {
    colorCuerpo: { type: 'string' },
    colorBorde: { type: 'string' }
  },

  init: function () {

    let el = this.el

    // Wait for model to load.
    el.addEventListener('model-loaded', () => {

      //console.log(this.data.colorCuerpo)
      let c1 = this.data.colorCuerpo
      let c2 = this.data.colorBorde

      let mesh = el.object3D

      mesh.traverse(function (node) {
        if (node.isMesh) {
          //console.log(node.material.name)
          if (node.material.name === "Estantes_Color") {
            //console.log(node)
            node.material.color.set(c1)

          }
          if (node.material.name === "Borde_Estantes" || node.material.name === 'Borde_3' || node.material.name === 'Borde_Estante_Trans' || node.material.name === 'Borde_Estante') {
            //console.log(node)
            node.material.color.set(c2)

          }

        }
      })
      /* // Grab the mesh / scene.
      const obj = el.getObject3D('mesh');
      // Go over the submeshes and modify materials we want.
      obj.traverse(node => {
        if (node.name.indexOf('ship') !== -1) {
          node.material.color.set('red');
        }
      }); */
    });
  }
});


AFRAME.registerComponent('locales-data', {
  schema: {
    localsData: { type: 'string' }
  },
  init: function () {
    const url = "https://d3iqhfs7sfm8r5.cloudfront.net/"
    let el = this.el
    let mesh = el.object3D
    let locales = JSON.parse(this.data.localsData)
    let color = locales[0].color_local
    let textura = locales[0].textura_local
    let logo = locales[0].imagen_aviso_local

    if(logo!=='imagen_aviso_local'){

      let logoTexture = new THREE.TextureLoader().load(
        url+logo);

        let mat = new THREE.MeshBasicMaterial({

          /* color: color,
          name: "logo-Local1", */
          /* roughness: 1,
          metalness: 0.1, */
          map: logoTexture,
        });

        document.getElementById('logo-Local1').object3D.traverse(function (node) {
          if (node.isMesh) {
            
            node.material = mat
            //console.log(node.material)
          }
        })
      
      
    }



    let localTexture = null

    if (textura !== '') {
      localTexture = new THREE.TextureLoader().load(
        require('../../../assets/images/Locales/texturas/' + textura));
      localTexture.wrapS = THREE.RepeatWrapping;
      localTexture.wrapT = THREE.RepeatWrapping;
      localTexture.repeat.set(50, 50)
      localTexture.rotation = 0.875;
      localTexture.name = textura
    }



    let Local = new THREE.MeshStandardMaterial({

      color: color,
      name: "Local1",
      roughness: 1,
      metalness: 0.1,
      map: localTexture,
    });

    if (mesh) {
      mesh.traverse(function (node) {
        if (node.isMesh) {
          //console.log(node.material.name)
          if (node.material.name === "Local1") {
            //console.log(node)
            node.material = Local

          }


        }
      })
    }
  }

});