import axios from "axios";

export const updatePubli = (obj) => {

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    let logo = getLogo(obj.clave_publicidad)
    //console.log(obj.clave_publicidad)

    const body = {
        "request_data": {
            "type_action": "update_pu",
            "data": {
                "id_publicidad": obj.id_publicidad,
                "id_marketplace": obj.id_marketplace,
                "clave_publicidad": obj.clave_publicidad,
                "imagen_publicidad": logo,
                "posicion_publicidad": obj.posicion_publicidad
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization": window.localStorage.getItem('adminToken')
        }
    }

    // Axios
    axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'marketplaces', body, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
        .then((response) => {


            if (response.status == '200' && response.data.status != 'failed') {

                console.log(response)
                alert("Se ha guardado correctamente en la base de datos")

            }
        })
        .catch(error => {
            console.error('error : ', error)
        });

}



const getLogo = (id) => {
    let logoimage = null
    let panel1 = document.getElementById('publi-'+id+'1')
    //let panel2 = document.getElementById('publi-'+id+'2')

    panel1.object3D.traverse(function (node) {
        if (node.isMesh) {

            //node.material = mat
            if(node.material.map){
                logoimage = node.material.map.image.currentSrc
            }
            
        }
    })

    if(logoimage){
        if (!logoimage.startsWith('data')) {
            logoimage = null
        }
    }
    


    return logoimage
}