import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';



//icons
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import InfoRoundedIcon from '@mui/icons-material/InfoRounded';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';

import PersonalCard from '../../Cards/Card';
import MainPubli from './MainPubli';

export default function PublicidadTab() {
    const [infoOpen, setInfoOpen] = React.useState(false);
    const [publiSelected, setPubliSelected] = React.useState("");
    const [infoPubli, setInfoPubli] = React.useState();

    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "15%",

        padding: '2rem',
    }

    const myStyle = {
        display: "flex",
        height: "100%",
        minWidth: "20%",
        flexDirection: 'column',
        padding: '1rem',

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        minWidth: "55%",
        flexDirection: 'column',
        padding: '1rem'
    }

    const myStyle3 = {
        display: "flex",
        height: "100%",
        width: "30%",
        padding: '1rem 1rem'
    }

    const myStyleValla = {
        display: "flex",
        height: "100%",
        with: "25%",
        padding: '1rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyleValla2 = {
        display: "flex",
        height: "100%",
        width: "30%",

        padding: '1rem'
    }

    const cameraPositions = {
        A:"-26.588 1.607 16.698",
        B:"-14.764 1.6 5.782",
        C:"-26.406 1.6 -9.124",
        D:"-3.107 1.6 -24.86",
        E:"7.839 1.6 -12.134",
        F:"21.071 1.6 -23.292",
    }

    function openPubli(id, obj) {
        setInfoOpen(true)
        setPubliSelected(id)
        setInfoPubli(obj)

        let camera = document.getElementById("publi-camera")
        camera.setAttribute('orbit-controls',{target:cameraPositions[id]})
        camera.setAttribute('camera', 'active', true)
    }

    function clickBack() {
        setInfoOpen(false)
        document.getElementById("player-camera").setAttribute('camera', 'active', true)
    }

    function loadCards(){
        const arr = JSON.parse(localStorage.getItem('arrpubli'));
        let rows = [];
        let clave
        let id

        arr.forEach(el => {
            clave = el.clave_publicidad
            id = el.id_publicidad

            rows.push(
                <PersonalCard key={"k"+clave} arrobject={el} number={clave} handleClick={openPubli}></PersonalCard>
            )
            
        });

        return <>{rows}</>
    }

    return (
        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
            {!infoOpen &&
                <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
                    <Box sx={myStyle}>
                        <Box sx={{ display: "flex", justifyContent: "space-between" }}>
                            <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Presentacin</Typography>
                            <InfoRoundedIcon sx={{ mr: 1, color: "white" }} fontSize="small" />
                        </Box>

                        <Box sx={{ display: "flex", alignItems: "center", width: "100%", height: "70%" }}>
                            <Box sx={{
                                height: "3rem",
                                width: "45%",
                            }}>
                                <img src={require("../../../../assets/images/Locales/Vista1_1_1.jpg")} style={{ width: "100%", height: "100%", objectFit: "scale-down" }}></img>
                            </Box>
                            <Box>
                                <Box sx={{ display: "flex", marginTop: "0.5rem", marginLeft: "0.2rem" }}>
                                    <FolderRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                    <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Elegir archivo</Typography>
                                </Box>

                                <Box sx={{ display: "flex", marginTop: "0.5rem", marginLeft: "0.2rem" }}>
                                    <PhotoCameraIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                    <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Tomar foto</Typography>
                                </Box>
                            </Box>
                        </Box>

                    </Box>
                    <Divider orientation="vertical" variant="middle" flexItem />
                    <Box sx={myStyle2}>
                        <Typography sx={{ color: 'white' }} variant='h6'>Vallas publicitarias</Typography>
                        <Box sx={{ display: "flex", marginTop: "0.5rem", justifyContent: 'space-between', width: "100%" }}>

                            <Box sx={{ display: "flex" }}>
                                {loadCards()}
                                {/* <PersonalCard number="A" handleClick={openPubli}></PersonalCard>
                                <PersonalCard number="B" handleClick={openPubli}></PersonalCard>
                                <PersonalCard number="C" handleClick={openPubli}></PersonalCard>
                                <PersonalCard number="D" handleClick={openPubli}></PersonalCard>
                                <PersonalCard number="E" handleClick={openPubli}></PersonalCard>
                                <PersonalCard number="F" handleClick={openPubli}></PersonalCard> */}
                            </Box>

                            <Box sx={{ display: 'flex', alignContent: 'flex-end' }}>
                                <Typography sx={{ color: 'white' }}>Agregar colaborador</Typography>
                                <AddCircleOutlineRoundedIcon sx={{ mr: 1, color: "white" }} />
                            </Box>
                        </Box>

                    </Box>
                </Box>
            }

            {/* Informacin de la publicidad ******** */}

            {infoOpen &&
                <MainPubli handleClickBack={clickBack} idPubli={publiSelected} arrobject={infoPubli}>

                </MainPubli>
            }

        </Box>
    );
}