/**
 * Este componente espera la carga de un modelo 3D (GLB) para aplicarle la maya de colisin
 * Es recomendable usar un modelo con pocos polgonos para que no afecte el rendimiento.
 */
  

AFRAME.registerComponent("load-glb-collision", {

    schema: {
        model: {default: ''},
        body: {type: 'string', default: 'static'}, //dynamic: A freely-moving object.
        shape: {type: 'string', default: 'hull'}, // hull: Wraps a model in a convex hull, like a shrink-wrap
    },
    init() {
        this.el.setAttribute('gltf-model', this.data.model)
        this.el.setAttribute('shadow', {receive: false})
    
        /* // Especificar el tipo de body a definir (dynamic, static, kinematic)
        this.el.setAttribute('ammo-body', {type: this.data.body, scaleAutoUpdate:true});
    
        // Esperar a la carga del modelo para aplicar ammo-shape (box, cylinder, sphere, capsule, cone, hull)
        this.el.addEventListener('model-loaded', () => {
            this.el.setAttribute('ammo-shape', {type: this.data.shape});
        }) */
    },
});