export const changeMat = (color) => {
    //console.log(color)
    let modelmall = document.getElementById('mall-model')
    let mesh = modelmall.object3D

    let Local1 = new THREE.MeshStandardMaterial({
        
        color: color,
        name:"Local1",
        roughness: 1,
        metalness:0.1,

    });

    mesh.traverse(function(node){
        if (node.isMesh){  
            if(node.material.name==="Local1"){
                //console.log(node)
                //node.material.color.set(color)
                node.material = Local1
                
            }
        
        }
    })
    //console.log(mesh)
}

export const changeColor = (color, meshMat, clave) => {
    //console.log(color)
    let modelShelf = document.getElementById('shelf'+clave)
    //console.log(meshMat)
    let mesh = modelShelf.object3D

    let shelfMat = new THREE.MeshStandardMaterial({
        
        color: color,
        name: meshMat,
        roughness: 1,
        metalness:0.1,

    });

    mesh.traverse(function(node){
        if (node.isMesh){  
            //console.log(node.material.name)
            if(node.material.name===meshMat){
                //console.log(node)
                node.material=shelfMat
                       
            }

            //PARA GONDOLAS TIPO 3 ytipo2 ***************
            if(meshMat==='Borde_Estantes' && (node.material.name=== 'Borde_3' || node.material.name=== 'Borde_Estante_Trans'  || node.material.name=== 'Borde_Estante')){
                //console.log(node)
                node.material=shelfMat
                       
            }
        
        }
    })
    //console.log(mesh)
}


export const changeTexture = (texture) => {
    //console.log(color)
    let modelmall = document.getElementById('mall-model')
    let mesh = modelmall.object3D

    const localTexture = new THREE.TextureLoader().load( 
        require('../../../assets/images/Locales/texturas/'+texture ));
    localTexture.wrapS = THREE.RepeatWrapping;
    localTexture.wrapT = THREE.RepeatWrapping;
    localTexture.repeat.set( 50, 50 )
    localTexture.rotation = 0.875;
    localTexture.name = texture

    let Local1 = new THREE.MeshStandardMaterial({
        
        color: "#FFFFFF",
        name:"Local1",
        roughness: 1,
        metalness:0.1,
        map:localTexture,
    });

    mesh.traverse(function(node){
        if (node.isMesh){  
            if(node.material.name==="Local1"){
                //console.log(node)
                node.material=Local1
                
                
            }
        
        }
    })
    //console.log(mesh)
}

export const changeLogo = (imageData) =>{
    let panelLogo = document.getElementById('logo-Local1')
    //panelLogo.setAttribute("src",imageData)

    let logoTexture = new THREE.TextureLoader().load(
        imageData);

        let mat = new THREE.MeshBasicMaterial({

          /* color: color,
          name: "logo-Local1", */
          /* roughness: 1,
          metalness: 0.1, */
          map: logoTexture,
        });

        panelLogo.object3D.traverse(function (node) {
          if (node.isMesh) {
            
            node.material = mat
            //console.log(node.material)
          }
        })
    
}

export const changePubliImage = (id,imageData) =>{
    let panel1 = document.getElementById('publi-'+id+'1')
    let panel2 = document.getElementById('publi-'+id+'2')
    /* panel1.setAttribute("src",imageData)
    panel2.setAttribute("src",imageData) */

    let publiTexture = new THREE.TextureLoader().load(
        imageData);

        let mat = new THREE.MeshBasicMaterial({

          map: publiTexture,
        });

        panel1.object3D.traverse(function (node) {
          if (node.isMesh) {
            
            node.material = mat
            //console.log(node.material)
          }
        })

        panel2.object3D.traverse(function (node) {
            if (node.isMesh) {
              
              node.material = mat
              //console.log(node.material)
            }
          })
}

export const setInitPubli= (data) =>{

    const url = "https://d3iqhfs7sfm8r5.cloudfront.net/"

    data.forEach(el => {
        if(el.imagen_publicidad!=='imagen_publicidad'){
            let panel1 = document.getElementById('publi-'+el.clave_publicidad+'1')
            let panel2 = document.getElementById('publi-'+el.clave_publicidad+'2')

            let publiTexture = new THREE.TextureLoader().load(
                url+el.imagen_publicidad);
        
                let mat = new THREE.MeshBasicMaterial({
        
                  map: publiTexture,
                });
        
                panel1.object3D.traverse(function (node) {
                  if (node.isMesh) {
                    
                    node.material = mat
                    //console.log(node.material)
                  }
                })
        
                panel2.object3D.traverse(function (node) {
                    if (node.isMesh) {
                      
                      node.material = mat
                      //console.log(node.material)
                    }
                  })
        }
    });



    
}