import axios from "axios";

export const updateLocales = (local) =>{

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    let color = getColor(local)
    let textura = getText(local)
    let logo = getLogo(local)
    //console.log(logo)
    
    const body ={
        "request_data": {
          "type_action": "update",
          "data": {
            "id_local": "b157ce22-412c-4bd9-8cd2-fc22699e0c59",
            "id_empresa_usuario_local": "511095a8-da38-4acd-9254-90f82c75fb5b",
            "id_marketplace": "aa7b8c56-959a-435b-9b20-c2f9b2dbd899",
            "imagen_aviso_local": logo,
            "nombre_local": "Hoyostools",
            "descripcion_local": "Vendemos de todos los productos",
            "color_local": color,
            "textura_local": textura
          }
        },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization": window.localStorage.getItem('adminToken')
      }
    }

    // Axios
    axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'locales', body, {
      headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
      },
    })
    .then((response) => {
        

      if(response.status == '200' && response.data.status != 'failed'){
        
        console.log(response)
        alert("Se ha guardado correctamente en la base de datos")

      }
    })
    .catch(error => {
        console.error('error : ', error)
    });

  }

  const getColor = (local) =>{

    let mallmodel = document.getElementById('mall-model')
    let mesh = mallmodel.object3D
    let mycolor

        mesh.traverse(function(node){
            if (node.isMesh){  
                //console.log(node.material.name)
                if(node.material.name===local){
                    //console.log(node)
                    mycolor = '#'+ (node.material.color).getHexString()
                           
                }
               
            
            }
        })
    return mycolor
}

const getText = (local) =>{

    let mallmodel = document.getElementById('mall-model')
    let mesh = mallmodel.object3D
    let mytext

        mesh.traverse(function(node){
            if (node.isMesh){  
                //console.log(node.material.name)
                if(node.material.name===local){
                    //console.log(node)
                    //mycolor = '#'+ (node.material.color).getHexString()
                    mytext=node.material.map?node.material.map.name:""
                           
                }
               
            
            }
        })
    return mytext
}

const getLogo = (local) =>{
    let logoimage = null
    let logopanel = document.getElementById('logo-'+local)
    logopanel.object3D.traverse(function (node) {
        if (node.isMesh) {
          
          //node.material = mat
          logoimage = node.material.map.image.currentSrc
        }
      })

    if(!logoimage.startsWith('data')){
        logoimage = null
    }
    

    return logoimage
}