import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import CustomCard from '../../Cards/CustomCard';
import MainLocal from './MainLocal';



export default function ConfigLocales() {
    const [infoOpen, setInfoOpen] = React.useState(false);

    function openLocal() {
        setInfoOpen(true)

        //document.getElementById("player-camera").setAttribute('camera', 'active', false)
        document.getElementById("locales-camera").setAttribute('camera', 'active', true)

        
    }

    function backLocal() {
        setInfoOpen(false)

        document.getElementById("player-camera").setAttribute("camera",{active:true})
        //document.getElementById("locales-camera").setAttribute("camera",{active:false})

    }


    return (
        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
            <Box sx={{ width: '15%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Configuracin locales</Typography>
            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />

            {/* Dividir en componentes */}
            <Box sx={{ width: '85%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                {!infoOpen &&
                    <Box sx={{ display: "flex", alignItems: "center", padding: "0.2rem 1rem", width: "100%", justifyContent: "flex-start" }}>

                        <CustomCard size="md" number="1" type={2} image="Local2_1.jpg" handleClick={openLocal}></CustomCard>
                        
                    </Box>
                }
                {infoOpen &&
                    <MainLocal handleClickBack={backLocal}></MainLocal>
                }


            </Box>
        </Box>
    )
}