import React from 'react';
import { styled, useTheme } from '@mui/material/styles';
import './BottomMenu.css'

import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';

//icons
import CorporateFareRoundedIcon from '@mui/icons-material/CorporateFareRounded';
import StoreRoundedIcon from '@mui/icons-material/StoreRounded';
import CalendarViewDayRoundedIcon from '@mui/icons-material/CalendarViewDayRounded';
import LocalMallRoundedIcon from '@mui/icons-material/LocalMallRounded';


import ConfigMall from '../ConfigMall/ConfigMall';
import ConfigLocales from '../ConfigLocales/ConfigLocales';
import ConfigEstanterias from '../ConfigEstanterias/ConfigEstanterias';
import ConfigProductos from '../ConfigProductos/ConfigProductos';


export default function BottomMenu() {
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const [configMall, setConfigMall] = React.useState(true);
    const [configLocales, setConfigLocales] = React.useState(false);
    const [configEstantes, setConfigEstantes] = React.useState(false);
    const [configProductos, setConfigProductos] = React.useState(false);

     function changeMenu(id){
        document.getElementById("arrow1").classList.remove("o-arrow-active")
        document.getElementById("arrow2").classList.remove("o-arrow-active") 
        document.getElementById("arrow3").classList.remove("o-arrow-active") 
        document.getElementById("arrow4").classList.remove("o-arrow-active")   


        setConfigMall(false)
        setConfigLocales(false)
        setConfigEstantes(false)
        setConfigProductos(false)

        let camera1 = document.getElementById("player-camera")
        let mallModel = document.getElementById("mall-model")
        let localModel = document.getElementById("local-model")
        let publiCont = document.getElementById("publicidad")
        let shelfsCont = document.getElementById("shelfs-3D-container")


        /******* FALTA: CREAR FUNCIN PARA ACTIVAR/DESACTIVAR CMARAS/MODELOS***************/
        switch (id) {
            case 'btn-centro':
                camera1.setAttribute("camera",{active:true})
                mallModel.setAttribute("visible",true)
                publiCont.setAttribute("visible",true)
                localModel.setAttribute("visible",false)
                shelfsCont.setAttribute("visible",false)
                setConfigMall(true)
                document.getElementById("arrow1").classList.add("o-arrow-active")
              break;
            case 'btn-locales':
                camera1.setAttribute("camera",{active:true})
                mallModel.setAttribute("visible",true)
                publiCont.setAttribute("visible",true)
                localModel.setAttribute("visible",false)
                shelfsCont.setAttribute("visible",false)
                setConfigLocales(true)
                document.getElementById("arrow2").classList.add("o-arrow-active")
              break;
            case 'btn-estanterias':
                camera1.setAttribute("camera",{active:true})
                mallModel.setAttribute("visible",false)
                publiCont.setAttribute("visible",false)
                localModel.setAttribute("visible",true)
                shelfsCont.setAttribute("visible",true)
                setConfigEstantes(true)
                document.getElementById("arrow3").classList.add("o-arrow-active")
              break;
            case 'btn-productos':
                camera1.setAttribute("camera",{active:true})
                mallModel.setAttribute("visible",false)
                publiCont.setAttribute("visible",false)
                localModel.setAttribute("visible",true)
                shelfsCont.setAttribute("visible",true)
                setConfigProductos(true)
                document.getElementById("arrow4").classList.add("o-arrow-active")
              break;
            default:
              console.log("error");
          }
     }
    
    

    return (
        <Box sx={{ display: 'flex', position:'absolute', bottom:0 }}>
          {/* <CssBaseline /> */}
          
          <Box className='interactable-ui' sx={{
            width: '100vw',
            height: 150,
            backgroundColor: '#545454',
            display:'flex',
            
            }}>
                {/* Men lateral */}
                <Box sx={{ width:'3%', height:'100%', display:'flex', flexDirection:"column" ,alignItems:'center', justifyContent:'space-around', backgroundColor:"#ea660e"}}>
                    <IconButton aria-label="config-general" onClick={()=>changeMenu("btn-centro")} sx={{padding:0}}>
                        <CorporateFareRoundedIcon sx={{ mr: 1 , color:"white"}} />
                        
                    </IconButton>
                    <IconButton aria-label="config-local" onClick={()=>changeMenu("btn-locales")} sx={{padding:0}}>
                        <StoreRoundedIcon sx={{ mr: 1 , color:"white"}} />
                    </IconButton>
                    <IconButton aria-label="config-local" onClick={()=>changeMenu("btn-estanterias")} sx={{padding:0}}>
                      <CalendarViewDayRoundedIcon sx={{ mr: 1 , color:"white"}} />
                    </IconButton>
                    <IconButton aria-label="config-local" onClick={()=>changeMenu("btn-productos")} sx={{padding:0}}>
                      <LocalMallRoundedIcon sx={{ mr: 1 , color:"white"}} />
                    </IconButton>
                    
                </Box>

                <Box sx={{ width:'0%', height:'100%', display:'flex', flexDirection:"column" ,alignItems:'center', justifyContent:'space-around'}}>
                  <div id="arrow1" className="arrow-right o-arrow-active"></div>
                  <div id="arrow2" className="arrow-right"></div>
                  <div id="arrow3" className="arrow-right"></div>
                  <div id="arrow4" className="arrow-right"></div>
                </Box>

                {configMall &&
                    <ConfigMall></ConfigMall>
                }
                {configLocales &&
                    <ConfigLocales></ConfigLocales>
                }
                {configEstantes &&
                    <ConfigEstanterias></ConfigEstanterias>
                }
                {configProductos &&
                    <ConfigProductos></ConfigProductos>
                }
                

                
          </Box>
        </Box>
      );
}