import React, { useEffect } from 'react';

//  Componentes
import PlaneImageProduct from './PlaneProduct';
import addLogo from '../../../assets/images/logos_loacles/addLogo.jpg'

import { getProducts } from '../../../server/DataProducts';

export default function Shelf3D2({ keyN, position, rotation, color1, color2, logos, id }) {
    const [visibleLogos, setVisibleLogos] = React.useState(false);
    const [visibleProducts, setVisibleProducts] = React.useState(false);

    const [productsLoaded, setProductsLoaded] = React.useState(false);
    const [firstL, setFirstL] = React.useState(false);
    const [productsList, setProductsList] = React.useState([]);

    const url = "https://d3iqhfs7sfm8r5.cloudfront.net/"

    React.useEffect(() => {

        document.addEventListener('load_shelf', function (e) {
            
            if (e.detail.idShelf === keyN) {
                //console.log(logos.superior1);
                if (e.detail.dataVisible === 'Products') {
                    setVisibleProducts(true)
                    setVisibleLogos(false)
                    setFirstL(true)
                } else {
                    setVisibleProducts(false)
                    setVisibleLogos(true)
                }
            }
        })

        document.addEventListener('leave_shelf', function (e) {
            //if (e.detail.idShelf === keyN) {
                setVisibleProducts(false)
                setVisibleLogos(false)
                //console.log(logos.superior1);
            //}
        })

        document.addEventListener('reload_shelf', function (e) {
            
           
            if (e.detail.idShelf === id) {
                console.log("reload shelf")
                setProductsLoaded(false)
                getP()
                setProductsLoaded(false)
                
            }
        })


    }, []);

    React.useEffect(() => {

        if(firstL){
           getP()
        }

    },[firstL])

    function getP(){
        let aux = getProducts(id, "tipo2")
        aux.then( response => {
            if(response.status == '200' && response.data.status != 'failed'){
                //obtengo el arreglo de productos
                
                let productList = response.data?.body?.data || []
                //console.log(productList)
                setProductsLoaded(true)
                setProductsList(productList)
                
            }
        }
        ).catch( err => {
            console.log(err)
        }

        )
    }

    function loadProducts (){
        let rows = [];
        let ubi
        let image

        productsList.forEach(element => {
            ubi = JSON.parse(element.posicion_producto)
            image = element.imagen_producto

            rows.push(
            <PlaneImageProduct
                        position={ubi.position.x+" "+ubi.position.y+" "+ubi.position.z}
                        rotation={ubi.rotation.x+" "+ubi.rotation.y+" "+ubi.rotation.z}
                        width={ubi.width} 
                        height={ubi.height}
                        image={image=='imagen_producto'?addLogo:url+image}
                        name={image=='imagen_producto'?"Producto no asignado":"Producto asignado"} //cambiar por nombre
                        vis={visibleProducts} 
                        id={element.id_producto}
                        arrobj={JSON.stringify(element)}
                        key={"k"+element.id_producto} />)
        });

        return <a-entity id="products-container">{rows}</a-entity>;
    }

    return (
        <a-entity id={"shelf-" + keyN + "-container"} scale="1 1 1" position={position} rotation={rotation}>

            {/* Texto con el id */}
            <a-entity position="0 3.5 0" scale="2 2 2" visible="true"
                text={"value:" + keyN + "; align:center; font:mozillavr; width:0.5; wrapCount:3"}
                look-at="#estantes-camera">
            </a-entity>

            <a-entity
                 id={"shelf" + keyN}
                className="shelf-2-model"
                scale="50 50 50"
                gltf-model="#model-asset-shelf-2"
                ammo-body="type: static; "
                ammo-shape="type: box; fit:manual; halfExtents: 0.41 1.8 1; offset:0 0.9 0"
                initial-materials={"colorCuerpo: "+color1+"; colorBorde: "+color2}>

            </a-entity>
            <a-image
                id={"superior1-"+keyN}
                src={logos=='banner_superior_gondola_tipo_2'?addLogo:url+logos} width="1.95" height="0.46"
                adjust-image="maxH:0.46; maxW:1.95"
                class={visibleLogos?"clickable product-entity":""}
                interactable-logo
                position="-0.243 2.484 0" rotation="10 90 0"
                visible={visibleLogos}>
            </a-image>

            {/* Collider para activar productos */}
            <a-sphere
                color="green"
                visible="false"
                ammo-body="
                    type: static; 
                    emitCollisionEvents:true; 
                    disableCollision:true"
                ammo-shape="type:sphere"
                radius='5'
                lod-shelf="selectorToHide: #products-container;">

            </a-sphere>

            {/* Productos */}
            
              
            {productsLoaded &&
                loadProducts()
            }
            


        </a-entity>
    );
}