import axios from "axios";
import addLogo from '../assets/images/logos_loacles/addLogo.jpg'

export const updateProduct = (obj, image, codigo, fromList) => {
    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    /* console.log(image)
    console.log(codigo) */
    //console.log(obj.id_producto)

    let imagenP
    let codigoP
    
    if(image.startsWith('data')){
        imagenP = image
        codigoP = codigo
    }else{
        imagenP = null
        codigoP = obj.codigo_producto
    }


    const bodyUpdate = {
        "request_data": {
          "type_action": "update",
          "data": {
            "id_producto": obj.id_producto,
            "id_empresa_usuario_producto": obj.id_empresa_usuario_producto,
            "id_gondola_tipo1_producto": obj.id_gondola_tipo1_producto,
            "id_gondola_tipo2_producto": obj.id_gondola_tipo2_producto,
            "id_gondola_tipo3_producto": obj.id_gondola_tipo3_producto,
            "imagen_producto": imagenP,
            "nombre_producto": obj.nombre_producto,
            "descripcion_producto": obj.descripcion_producto,
            "codigo_producto": codigoP,
            "id_categoria_producto": obj.id_categoria_producto,
            "cantidad_por_caja_master_producto": obj.cantidad_por_caja_master_producto,
            "cantidad_por_caja_interna_producto": obj.cantidad_por_caja_interna_producto,
            "codigo_de_barras_producto": obj.codigo_de_barras_producto,
            "precio_producto": obj.precio_producto,
            "posicion_producto":obj.posicion_producto
          }
        },
        "headers": {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "Authorization": window.localStorage.getItem('adminToken')
        }
      }

    // Axios
    return axios.post(url + 'productos', bodyUpdate, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
        .then(response => {
            //console.log('respuesta :', response)

            if (response.status == '200' && response.data.status !== 'failed') {

                if(!fromList){
                    alert("Actualizado correctamente en la base de datos.") 
                }
                console.log("agregado producto")
                 

            }
        })
        .catch(error => {
            console.log('error : ', error)
        });
}

